@font-face {font-family: "Exo2"; font-weight: "bold"; src: url("./fonts/Exo2-ExtraBold.ttf")};
@font-face {font-family: "JosefinSans"; font-weight: 300; src: url("./fonts/JosefinSans-Light.ttf")};
@font-face {font-family: "JosefinSans"; font-weight: 300; font-style: italic; src: url("./fonts/JosefinSans-LightItalic.ttf")};
@font-face {font-family: "JosefinSans"; font-weight: 400; src: url("./fonts/JosefinSans-Regular.ttf")};
@font-face {font-family: "JosefinSans"; font-weight: 500; src: url("./fonts/JosefinSans-Medium.ttf")};
@font-face {font-family: "JosefinSans"; font-weight: 500; font-style: italic; src: url("./fonts/JosefinSans-MediumItalic.ttf")};
@font-face {font-family: "JosefinSans"; font-weight: 700; src: url("./fonts/JosefinSans-Bold.ttf")};

a {
    text-decoration: none;
    color: inherit;
}

.App {
    display: flex;
    position: relative;
    flex-direction: column;
    width: 100%;
}

// START NAVIGATION

#navbar {
    display: flex;
    flex-direction: row;
    position: fixed;
    z-index: 1;
    width: 100%;
    height: 15%;
    // background-color: rgba(0,0,0, 0.2);

    #navbarName {
        font-family: "Exo2";
        font-size: 18px;
        text-transform: uppercase;
        display: flex;
        position: relative;
        align-items: center;
        height: 100%;
        width: 38%;
        padding-left: 2%;
        // background-color: rgba(0,0,0, 0.4);
    }

    #navbarLogo {
        display: flex;
        position: relative;
        height: 100%;
        width: 20%;
        align-items: center;
        justify-content: center;
        #logo {
            width: 100px;
            height: 100px;
        }
        // background-color: rgba(255, 0, 0, 0.4);
    }

    #navbarLinks {
        display: flex;
        position: relative;
        height: 100%;
        width: 38%;
        justify-content: flex-end;
        padding-right: 2%;
        font-family: "JosefinSans";
        font-size: 18px;
        // background-color: rgba(0,0,0, 0.4);

        ul {
            display: flex;
            flex-direction: row;
            font-size: inherit;
            text-transform: uppercase;
            justify-content: center;
            align-items: center;
            list-style: none;

            li {
                padding: 0 10px;

                button {
                    text-transform: inherit;
                    font-family: inherit;
                    font-size: inherit;
                    border: none;
                    background: none;
                    text-decoration: none;
                }
                button:active {
                    color: inherit;
                }
                button > i {
                    pointer-events: none;
                }
            }
        }
    }
}

// END NAVIGATION

// START MOBILE NAVIGATION

#navbarMobile {
    display: flex;
    flex-direction: row;
    position: fixed;
    align-items: center;
    width: 100%;
    // height: 97.5px;
    height: 12vh;
    background-color: #fff;
    transition: transform 300ms ease;
    // background-color: rgba(255, 2, 0, 0.4);

    #navbarLogoMobile {
        display: flex;
        position: absolute;
        height: 100%;
        width: 100%;
        align-items: center;
        justify-content: center;
        // background-color: rgba(255, 0, 0, 0.4);

        img {
            width: 60px;
        }
    }

    #navButtonContainer {
        z-index: 1001;
        display: flex;
        position: fixed;
        width: 25%;
        // height: 97.5px;
        height: 12vh;
        align-items: center;
        justify-content: center;
        // background-color: rgba(0, 0, 0, 0.4);
        transform: scale(0.4);
    
        #navButton {
            z-index: inherit;
            display: flex;
            flex-direction: column;
            width: 70px;
            cursor: pointer;
    
            span {
                z-index: inherit;
                background: #000;
                border-radius: 10px;
                height: 4px;
                margin: 7px 0;
                transition: 400ms cubic-bezier(0.68, -0.60, 0.32, 1.60);
    
                &:nth-of-type(1) {
                    width: 50%;
                }
                &:nth-of-type(2) {
                    width: 100%;
                }
                &:nth-of-type(3) {
                    width: 75%;
                }
    
                &.show {
                    background: #fff;
        
                    &:nth-of-type(1) {
                        transform-origin: bottom;
                        transform: rotateZ(45deg) translate(8px, 0px);
                    }
                    &:nth-of-type(2) {
                        transform-origin: top;
                        transform: rotateZ(-45deg);
                    }
                    &:nth-of-type(3) {
                        transform-origin: bottom;
                        width: 50%;
                        transform: translate(30px, -6px) rotateZ(45deg);
                    }
                }
            }
        }
    }
    
    #flyoutMenu {
        z-index: 1000;
        width: 100vw;
        height: 100vh;
        top: 100px;
        left: 40px;
        background-color: transparent;
        position: fixed;
        transition: transform 300ms ease;
        overflow: scroll;
    
        &.hide {
            transform: translateX(-100vw);
        }
        &.show {
            transform: translateX(0vw);
            overflow: hidden;
    
        }
    
        h2 a {
            font-family: "JosefinSans";
            font-weight: 400;
            text-transform: uppercase;
            padding: 10px 0;
            color: white;
            margin-left: 15px;
            text-decoration: none;
        }
        h2 a:hover {
            color: rgba(0 ,0, 0, 0.4);
        }
    }
    
    #flyoutBackground {
        z-index: 1000;
        top: -142vmax;
        left: -142vmax;
        // top: 0;
        // left: 0;
        width: 284vmax;
        height: 284vmax;
        background-color: black;
        border-radius: 50%;
        position: fixed;
        transition: transform 300ms ease;
    
        &.hide {
            transform: scale(0);
            transform-origin: 50% 50%;
        }
        &.show {
            transform: scale(1);
            border-radius: 50%;;
        }
    }
}



// END MOBILE NAVIGATION

// START IMAGE GALLERY
