            background-image: url('./images/testimg1.png');
        }
        .gal-img2 {
            background-image: url('./images/testimg2.jpg');
        }
        .gal-img3 {
            background-image: url('./images/testimg3.jpg');
        }
        .gal-img4 {
            background-image: url('./images/testimg4.jpg');
        }
        .gal-img5 {
            background-image: url('./images/testimg5.jpg');
        }
     }

}

