.fade-in {
    animation: fade 0.8s ease-in forwards;
    opacity: 0;
}
@keyframes fade {
    50% {
        opacity: 0.01;
    }
    100% {
        opacity: 1;
    }
}

// START LOCATIONS SECTION

#locations-section {
    display: grid;
    grid-template-columns: 37.5vw 1fr;
    flex-direction: row;
    position: relative;
    align-items: center;
    justify-items: center;
    width: 100%;
    height: 120vh;
    overflow: hidden;
    // background-color: rgba(0, 0, 0, 0.2);

    #locations {
        font-family: "JosefinSans";
        transform: translateY(20%);
        // background-color: rgba(0, 0, 0, 0.1);

        ul li {
            list-style: none;
            text-transform: uppercase;
            text-align: center;
            font-size: 20px;
            padding: 50px 0;
            line-height: 24px;

            .locations-item {
                text-transform: capitalize;
                font-size: 16px;
            }

            button {
                text-transform: inherit;
                font-family: inherit;
                font-size: inherit;
                border: none;
                background: none;
                text-decoration: none;
            }
            button:active {
                color: inherit;
            }
            button:focus {
                outline: none;
            }
            button > i {
                pointer-events: none;
            }

            a, 
            #yelpNorthridge, 
            #yelpWinnetka, 
            #yelpChatsworth, 
            #facebookNorthridge,
            #facebookWinnetka,
            #facebookChatsworth {
                opacity: 0;
                visibility: hidden;
                transform: translateY(30px);
                transition: 
                    opacity 300ms ease,
                    visibility 300ms ease,
                    transform 400ms ease;
            }
            a, 
            #yelpNorthridge.fade,
            #yelpWinnetka.fade,
            #yelpChatsworth.fade,
            #facebookNorthridge.fade,
            #facebookWinnetka.fade,
            #facebookChatsworth.fade {
                opacity: 1;
                visibility: visible;
                transform: translateY(-10px);
            }
        }
    }

    #img-div {
        display: flex;
        position: relative;
        align-self: flex-end;
        overflow: hidden;
        width: 905px;
        height: 700px;
        // background-color: rgba(0, 0, 0, 0.1);
        #sfv-map {
            position: relative;
            object-fit: cover;
            min-width: 100%;
            min-height: 100%;
        }
        
        #img1 {
            position: absolute;
            width: 10px;
            height: 10px;
            z-index: 100;
            top: 193px;
            left: 415px;
            transition: 
                width 400ms ease,
                height 400ms ease,
                top 400ms ease,
                left 400ms ease;
        }
        #img1.enlarged {
            width: 40px;
            height: 40px;
            top: 178px;
            left: 400px;
        }

        #img2 {
            position: absolute;
            width: 10px;
            height: 10px;
            z-index: 100;
            top: 357px;
            left: 296px;
            transition: 
                width 400ms ease,
                height 400ms ease,
                top 400ms ease,
                left 400ms ease;
        }
        #img2.enlarged {
            width: 40px;
            height: 40px;
            top: 342px;
            left: 281px;
        }

        #img3 {
            position: absolute;
            width: 10px;
            height: 10px;
            z-index: 100;
            top: 275px;
            left: 212px;
            transition: 
                width 400ms ease,
                height 400ms ease,
                top 400ms ease,
                left 400ms ease;
        }
        #img3.enlarged {
            width: 40px;
            height: 40px;
            top: 260px;
            left: 197px;
        }
    }

}

