// CUSTOM CURSOR

* {
    cursor: none;
}

.cursor {
    position: fixed;
    left: 0;
    top: 0;
    pointer-events: none;
    height: 8px;
    width: 8px;
    border-radius: 50%;
    transition: 
      width 300ms ease,
      height 300ms ease,
      opacity 400ms ease;
  }
  .cursors .cursor:nth-child(1) {
    background-color: black;
    z-index: 2401;
    transform: translate(-50%, -50%);
  }
  .cursors .cursor:nth-child(2) {
    height: 24px;
    width: 24px;
    border: 2px solid black;
    border-radius: 50%;
    z-index: 2400;
    transform: translate(-50%, -50%);
  }
  .cursor:nth-child(2).enlarged {
    height: 50px;
    width: 50px;
  }
  .cursor:nth-child(1).clicked {
    height: 25px;
    width: 25px;
  }